import React from "react";

import { Combobox, ComboboxProps } from "../Combobox";

import { timeZonesWithGMT } from "./timeZoneSelect.utils";

export type TimeZoneOption = {
	label: string
	value: string
	offset: number
};

/**
 * TimeZoneSelect component
 * @param props - The props to pass to the Combobox component.
 * @returns The TimeZoneSelect component.
 */
const TimeZoneSelect = ({ styles, ...props }: ComboboxProps<TimeZoneOption>) => (
	<Combobox
		options={timeZonesWithGMT}
		comboboxLeftIcon="Globe"
		styles={{ ...styles, menu: () => ({ width: "fit-content" }), option: () => ({ whiteSpace: "nowrap" }) }}
		{...props}
	/>
)
;

export default TimeZoneSelect;
